<template>
    <div>
        <h1 style="font-size: 70px"> 게시물을 {{ updateFlag === 1 ? '수정' : '작성' }}하세요 ! </h1>
        
        <h2>제목</h2>
        <br> 
        <input v-model="subject" placeholder="제목"> 
        <br/>
        <br/>
        <br/>
        <h2>내용</h2>
        <br>
        <textarea class="textarea" v-model="content" placeholder="내용"/>
        <br>
        <br>
        <v-btn class="submit" outlined color="indigo" @click="updateFlag === 1 ? update() : write()"> 작성 </v-btn>
    </div>
</template>

<script>
import store from '../store/index.js'
import router from '../router/index.js'
    export default {
        name : 'write',
        data() {
            const index = this.$route.params.contentNumber;
            const updateFlag = this.$route.params.updateFlag;
            return {
                index : index,
                updateFlag : updateFlag !== undefined ? 1 : 2,
                subject: index !== undefined ? store.state.contentList[index].subject : "",
                content: index !== undefined ? store.state.contentList[index].content : "",
                name: index !== undefined ? store.state.contentList[index].name : "",
                user: store.state.userInfo
            }
        },
        methods: {
            write() {
                store.state.contentList.push({
                    num: store.state.contentList.length + 1,
                    subject: this.subject,
                    content: this.content,
                    name : store.state.userInfo.name
                })
                router.push({name : 'board'})
            },
            update(){
                store.state.contentList[this.index].subject = this.subject,
                store.state.contentList[this.index].content = this.content,
                store.state.contentList[this.index].name = this.name

                router.push({name : 'board'})
            }
        }
    }
</script>

<style lang="scss" scoped>
    input {background : white;}
    .author { margin: 0px 20px;}
    .textarea { background: white;}
    .submit { width: 170px;}
</style>