
<template>
    <div>
        <header>
            <v-simple-table dark>
                <template v-slot:default>
                <thead>
                    <tr>
                        <th class="text-left">번호</th>
                        <th class="text-left">제목</th>
                        <th class="text-left">내용</th>
                        <th class="text-left">작성자</th>
                        <th class="text-left">보기</th>
                        <th class="text-left">수정</th>
                        <th class="text-left">삭제</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(data, index) in paginatedData" :key="index">
                        <td>{{data.num}}</td><td>{{data.subject}}</td><td>{{data.content}}</td><td>{{data.name}}</td>
                        <td><button @click="GetContent(data.num)">보기</button></td><td><button @click="updateData(data.num)">수정</button></td>
                        <td><button @click="deleteData(data.num)">삭제</button></td>
                        <!-- <div class="col" v-if="transaction.type === 'success'"><span style="color:green">{{transaction.type}}</span></div> -->
                    </tr>
                </tbody>
                </template>
            </v-simple-table>
        </header>

        <footer>
            <ul class="pagination" v-if="data.length > 10 || currentPage > 1">
                <li class="pagination-item" title="pagination-item 1">
                    <button type="button" @click="onClickFirstPage" :disabled="isInFirstPage">
                        <h2>First</h2>
                    </button>
                </li>

                <li class="pagination-item" title="pagination-item 2">
                    <button type="button" @click="onClickPreviousPage" :disabled="isInFirstPage">
                        <h2>Prev</h2>
                    </button>
                </li>


                <li class="pagination-item" title="pagination-item 3">
                    <button type="button" @click="onClickNextPage" :disabled="isInLastPage">
                        <h2>Next</h2>
                    </button>
                </li>

                <li class="pagination-item" title="pagination-item 1">
                    <button type="button" @click="onClickLastPage" :disabled="isInLastPage">
                        <h2>Last</h2>
                    </button>
                </li>
            </ul>
        </footer>
        
    </div>
</template>

<script>
import store from '../store/index.js'
import router from '../router/index.js'
    export default {
        name : 'transactions-paginated',
        data() {
            return {
                page: 1,
            }
        },
        props: {
            data : {
                type: Array,
                required: true,
            },
            maxVisibleButtons: {
                type: Number,
                required: false,
                default: 3
            },
            totalPages: {
                type: Number,
                required: true
            },
            total: {
                type: Number,
                required: true
            },
            perPage: {
                type: Number,
                required: true
            },
            currentPage: {
                type: Number,
                required: true
            }
        },
        computed: {
            paginatedData() {
                let start = (this.currentPage - 1) * this.perPage
                let end = start + this.perPage
                return this.data.slice(start, end)
            },
            startPage() {
                if (this.currentPage === 1) return 1   // 처음페이지면
                if (this.currentPage === this.totalPages) return this.totalPages - this.maxVisibleButtons + (this.maxVisibleButtons - 1) // 마지막 페이지면
                return this.currentPage - 1
            },
            endPage() { return Math.min(this.startPage + this.maxVisibleButtons -1, this.totalPages) },
            pages() {
                let range = []
                for (let i = this.startPage; i <= this.endPage; i+=1){
                    range.push({
                        number: i,
                        isDisabled: i === this.currentPage
                    });
                }
                return range
            },
            isInFirstPage() { return this.currentPage === 1 },
            isInLastPage() { return this.currentPage === this.totalPages }
        },
        methods: {
            onClickFirstPage(){
                this.$emit('pagechanged', 1)
            },
            onClickPreviousPage(){
                this.$emit('pagechanged', this.currentPage - 1)
            },
            onClickPage(page){
                this.$emit('pagechanged', page)
            },
            onClickNextPage() {
                this.$emit('pagechanged', this.currentPage + 1)
            },
            onClickLastPage(){
                this.$emit('pagechanged', this.totalPages)
            },
            isPageActive(page) {
                return this.currentPage === page
            },

            updateData(num) {
                router.push({
                    name: 'write',
                    params: {
                        updateFlag : 1,
                        contentNumber : num-1
                    }
                })
            },
            deleteData(num) {
                let i = 1
                store.state.contentList.splice(num-1, 1)
                store.state.contentList.forEach(user => {
                    user.num = i
                    i++
                })
                console.log('삭제된 글의 번호:' , num)
                router.push({ name : 'board'})
            },
            GetContent(num){
                router.push({
                    name: 'content',
                    params: {
                        num: num
                    }
                })
            },
            
        }
    }
</script>

<style lang="scss" scoped>
    span { margin: 0px 30px;}
    footer { position: relative;}
    ul { list-style: none; position: absolute; left:650px; top:50px; }
    li { float: left; margin: 0px 30px;}
</style>