<template>
    
    <v-card class="mx-auto" max-width="400">
    <v-img
      class="white--text align-end"
      height="300px"
      width="1000px"
      src="https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"
    >
      <v-card-title class="subject">{{data.subject}} </v-card-title>
    </v-img>

    <v-card-subtitle class="num">글 번호 : {{data.num}}</v-card-subtitle>

    <v-card-text class="text--primary">
        <br>
        <hr/>
        <br>
      <div>{{data.content}}</div>
      <br>
        <hr/>
        <br>
      <div>{{data.name}}</div>
    </v-card-text>

    <v-card-actions>
      <v-btn
        color="orange"
        text
        @click="$router.push({name:'board'})"
      >
        뒤로가기
      </v-btn>

    </v-card-actions>
  </v-card>
</template>

<script>

import store from '../store/index.js'
export default {
    data() {
        const index = this.$route.params.num-1
        return {
            data: store.state.contentList[index]
        }
    }
}
</script>

<style lang="scss" scoped>
    .subject { font-size: 50px; }
    .num { font-size: 20px;}
</style>