
<template>
    <div>
        <h1>자유게시판</h1>
        <br>
        <div>
            <v-btn v-if="isLogin" x-large color="warning" class="write1" @click="$router.push({name: 'write'})" dark style="float:right; top:-50px"> 글쓰기 </v-btn>
        </div>
        <br>

        <transactions-paginated
            :data="trans"
            :total-pages="Math.ceil(trans.length / 10)"
            :total="trans.length"
            :per-page="10"
            :currentPage="currentPage"
            v-on:pagechanged="onPageChange">
        </transactions-paginated>
    </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import store from '../store/index.js'
import router from '../router/index.js'
import TransactionsPaginated from '../components/TransactionsPaginated.vue'
    export default {
        components:{
            TransactionsPaginated
        },
        data() {
            return {
                name: '',
                currentPage: 1,
            }
        },
        computed : {
            ...mapState(['contentList', 'isLogin', 'userInfo']),
            ...mapGetters(['trans'])
        },

        methods: {
            updateData(num) {
                router.push({
                    name: 'write',
                    params: {
                        updateFlag : 1,
                        contentNumber : num-1
                    }
                })
            },
            deleteData(num) {
                let i = 1
                store.state.contentList.splice(num-1, 1)
                store.state.contentList.forEach(user => {
                    user.num = i
                    i++
                })
                console.log('삭제된 글의 번호:' , num)
                router.push({ name : 'board'})
            },
            GetContent(num){
                router.push({
                    name: 'content',
                    params: {
                        num: num
                    }
                })
            },
            onPageChange(page){
                this.currentPage = page;
            }
        },
    }
</script>
<style lang="scss" scoped>
    th { padding: 0px 20px;}
    td { padding: 0px 20px;}
    .write1 { margin: 0px 20px; }
</style>